




















































import { Component, Vue, Ref, Watch } from "vue-property-decorator";
import GlobalHeader, {
  IHeaderType,
} from "@/components/global/GlobalHeader.vue";

interface IDayBtn {
  name: string;
  path: string;
  new: boolean;
}

@Component({
  components: {
    GlobalHeader,
  },
})
export default class WebtoonList extends Vue {
  @Ref() headerBar!: HTMLElement;
  private daySelected = 'happy';
  private sel!: HTMLElement;
  private sc = true;
  private fixTop = 0

  private headerType: IHeaderType = {
    cookieBtn: "Y",
    zzBtn: "Y",
    searchBtn: "Y",
    extraBtn: ["", "인기순", ""],
  };

  private weeks: IDayBtn[] = [
    {
      name: '신작',
      path: 'news',
      new: true,
    },
    {
      name: '월',
      path: 'mon',
      new: false,
    },
    {
      name: '화',
      path: 'tue',
      new: false,
    },
    {
      name: '수',
      path: 'wed',
      new: false,
    },
    {
      name: '목',
      path: 'thu',
      new: false,
    },
    {
      name: '금',
      path: 'happy',
      new: false,
    },
    {
      name: '토',
      path: 'sat',
      new: false,
    },
    {
      name: '일',
      path: 'sun',
      new: false,
    },
    {
      name: '완결',
      path: 'fin',
      new: false,
    },
  ]

  private goDay(target: string) {
    this.daySelected = target;
    console.log(target)
  }

  @Watch('daySelected')
  private selectedBG() {
    const btnRect = this.$el.querySelector(`.day-${this.daySelected}`)!.getBoundingClientRect();
    const dayFilter = this.$el.querySelector('.day-filter');
    this.sel.style.width = `${btnRect.width}px`;
    this.sel.style.left = `${btnRect.left + dayFilter!.scrollLeft}px`;
  }

  private scrollEvent() {
    const head = this.$el as HTMLElement;
    const banner = this.$el.querySelector('.banner') as HTMLElement;
    const topbar = this.$el.querySelector('.webtoon-topbar') as HTMLElement;
    if (document.scrollingElement!.scrollTop >= 95) {
      banner.style.height = '0';
      head.style.top = '0';
      topbar.style.position = 'fixed';
      head.style.paddingTop = '190px'
      return
    }
    head.style.paddingTop = '0'
    if (this.sc) {
      this.sc = false;
      requestAnimationFrame(this.headerEvent)
    }
  }

  private headerEvent() {
    const k = 1;
    const head = this.$el as HTMLElement;
    const banner = this.$el.querySelector('.banner') as HTMLElement;
    const inheader = this.$el.querySelector('.banner-header') as HTMLElement;
    const topbar = this.$el.querySelector('.webtoon-topbar') as HTMLElement;
    const scpx = document.scrollingElement!.scrollTop;
    topbar.style.position = 'relative';
    if (scpx < 0) {
      inheader.style.position = 'fixed';
      inheader.style.top = '0';
      head.style.top = `${this.fixTop+scpx}px`;
      banner.style.height = `200px`
    } else if (scpx <= 20) {
      inheader.style.position = 'fixed';
      inheader.style.top = '0';
      head.style.top = `${this.fixTop}px`;
      banner.style.height = `${200 - scpx*2}px`
    } else {
      inheader.style.position = 'absolute';
      inheader.style.top = '';
      inheader.style.bottom = '132px';
      head.style.top = `${this.fixTop - (this.fixTop-95)*(scpx-20)/75}px`;
      banner.style.height = `${160 - 160*(scpx-20)/75}px`
    }
    
    this.sc = true;
  }

  protected mounted() {
    this.$nextTick(()=>{
      this.fixTop = (this.$el as HTMLElement).getBoundingClientRect().top;
    })
    
    this.sel = this.$el.querySelector('.selected') as HTMLElement;
    this.selectedBG();
    document.addEventListener('scroll', this.scrollEvent);
    this.scrollEvent();
  }

  protected beforeDestroy() {
    document.removeEventListener('scroll', this.scrollEvent);
  }
}
